<template>
  <PopUp :flow='sbtyStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeSbty'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62f;</i>
          <span class="title-span">{{$t("device.sbty")}}</span>
        </div>
        <div class="content">
          <div class="form">
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("device.tyyy")}}：</span>
              </div>
              <div class="form-item-value">
                <el-input
                type="textarea"
                :autosize="{ minRows: 5, maxRows: 5}"
                :placeholder="$t('message.fill')"
                v-model="tyyy">
              </el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span></span>
              </div>
              <div class="form-item-value">
                <span class='tips'>{{$t("device.reason")}}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='deleteDeviceFn'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeSbty'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import {deleteDevice } from '@/api/device'
var that= {};
  export default {
    name: 'sbty',
      data(){
          return {
            tyyy:'',
          }
      },
      props:{
        stopDevice:{
          type:Object
        },
        sbtyStatus:{
          type:Boolean
        },
        getDeviceListFn:{
          type:Function,
          default: null
        }
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){

      },
      methods:{
        /*设备停用*/
        deleteDeviceFn(){
          console.log(that.stopDevice)
          var data={
            machineId:that.stopDevice.machineId,
            remark:that.tyyy
          }
          deleteDevice(data).then((res)=>{
            if(res.code==200 && res.success){
              that.success(that.$t("message.OperSuccess"));
              that.$emit('getsbtyStatus',!that.sbtyStatus);
              that.tyyy = '';
              that.getDeviceListFn();
            }
          })
        },
        closeSbty(){
          that.$emit('getsbtyStatus',!that.sbtyStatus);
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.content{
  margin:0 42px;
  display:flex;
  flex-direction: row;
  .form{
    padding:0 155px;
    .form-item{
      display:flex;
      margin-top:24px;
      .form-item-name{
        width:155px;
        span{
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(51,51,51,1);
          float:right;
          display:block;
          line-height:36px;
        }
      }
      .form-item-value{
        width:273px;
        .tips{
          font-size:16px;
          font-family:PingFangSC-Regular,PingFang SC;
          font-weight:400;
          color:rgba(24,144,255,1);
          margin-bottom:37px;
          float:left;
        }
      }
    }
  }
}
</style>



