import service from '../utils/http'
/*获取所有设备类型*/
export function getMachineTypeList(data) {
  return service({
    url: '/machine/machineName',
    method: 'get',
    params:data
  })
}
/*根据条件获取历史记录*/
export function getHistoryList(data) {
  return service({
    url: '/history/records',
    method: 'get',
    params:data
  })
}
/*获取规则*/
export function getRule(data) {
  return service({
    url: '/monitor/rule',
    method: 'get',
    params:data
  })
}
/*获取历史数据*/
export function getCurrentHistory(data) {
  return service({
    url: '/history/historyData',
    method: 'get',
    params:data
  })
}
/*批量导出*/
export function exportData(data) {
  return service({
    url: '/history/export',
    method: 'get',
    params:data
  })
}
/*某人导出*/
export function privateExportData(data) {
  return service({
    url: '/history/privateExport',
    method: 'get',
    params:data
  })
}



