<template>
  <PopUp :flow='czsbStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click="closeCzsb">&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62f;</i>
          <span class="title-span">{{$t("device.czsb")}}</span>
        </div>
        <div class="search-content">
          <div class="form">
            <div class="search-item">
              <div class="search-item-left">
                <span>{{$t("device.szks")}}：</span>
              </div>
              <div class="search-item-right">
                <el-select class='select' v-model='condition.departmentId' filterable :placeholder="$t('message.select')" @change='selectDepartment'>
                 <el-option
                   v-for="item in departmentList"
                   :key="item.departmentId"
                   :label="item.name"
                   :value="item.departmentId">
                 </el-option>
               </el-select>
              </div>
            </div>
            <div class="search-item">
              <div class="search-item-left">
                <span>{{$t("device.sblx")}}：</span>
              </div>
              <div class="search-item-right">
                <el-select class='select' v-model='condition.machineTypeId' filterable :placeholder="$t('message.select')" @change='selectMachineType'>
                 <el-option
                   v-for="item in machineTypeList"
                   :key="item.id"
                   :label="item.name"
                   :value="item.id">
                 </el-option>
               </el-select>
               <el-select class='select' v-model='condition.machineNoId' filterable :placeholder="$t('message.select')" @change='selectMachineNo'>
                <el-option
                  v-for="item in machineNoList"
                  :key="item.id"
                  :label="item.value"
                  :value="item.id">
                </el-option>
              </el-select>
              <a href="javascript:void(0)" class="search-btn" @click='searchMachineFn'>{{$t("device.ckdw")}}</a>
              </div>
            </div>
            <div class="rooms" v-if='roomList.list.length!=0 && roomList.length!=0'>
              <div class="room" v-for="(item,index) in roomList.list" :key='index'>
                <span>{{item.roomName}}</span>
                <img src="../../assets/img/device/ly.png" alt="" v-if='item.repeaterStatus==1'>
                <img src="../../assets/img/device/find.png" alt="" v-if='item.repeaterStatus==2'>
              </div>
            </div>
            <div class="res-row"  v-if='roomList.list.length!=0 && roomList.length!=0'>
              <div class="res-tips">
                <img src="../../assets/img/device/ly.png" alt="">
                <span>{{$t("device.lylyq")}}</span>
              </div>
            </div>
            <div class="result"  v-if='roomList.status==0||roomList.status==1||roomList.status==2'>
              <div class="result-h1">
                <h6>{{$t("device.ssResult")}}：</h6>
                <h6 v-if='roomList.status==1'><em>{{$t("device.noResult")}}</em><span>,{{$t("device.last")}}<em>{{roomList.roomName}}</em>{{$t("device.fj")}}</span></h6>
                <h6 v-if='roomList.status==2'><em>{{$t("device.noResult")}}</em></h6>
                <h6 v-if='roomList.status==0'>{{$t("device.fxsb")}}<em>{{roomList.roomName}}</em>{{$t("device.fj")}}</h6>
              </div>
              <div class="result-h2" v-if='roomList.status==1 || roomList.status==2'>{{$t("device.maybe")}}</div>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='closeCzsb'>{{$t("button.confirm")}}</a>
          <a href="javascript:void(0)" class="off" @click="closeCzsb">{{$t("button.close")}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import {searchDevice} from '@/api/device'
import {getMachineTypeList} from '@/api/history'
import {searchMachine,getMachineNo,getMachineModel} from '@/api/device'

var that= {};
  export default {
    name: 'czsb',
      data(){
          return {
            departmentList:that.$store.state.monitor.departmentList,
            condition:{
              departmentId:that.$store.state.monitor.departmentList[0].departmentId,
              machineTypeId:that.machineTypeList[0].id,
              machineNoId:'',
              type:''
            },
            roomList:{
              list:[]
            },
            machineNoList:[],
          }
      },
      props: {
          machineTypeList: {
              type: Array,
              default: function () {return []}
          },
          czsbStatus:{
            type:Boolean
          },
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){
        that.getMachineNoFn();
      },

      methods:{
        selectDepartment(){
          that.roomList = {
            list:[]
          };
        },
        selectMachineType(id){
          that.condition.machineTypeId = id;
          that.getMachineNoFn();
          that.roomList = {
            list:[]
          };
        },
        selectMachineNo(id){
          that.condition.machineNoId = id;
          that.roomList = {
            list:[]
          };
        },
        /*查询设备编号*/
        getMachineNoFn(){
          var data={
            machineTypeId:that.condition.machineTypeId
          }
          getMachineNo(data).then((res)=>{
             console.log(res);
            if(res.code==200 && res.success){
              that.machineNoList = res.data;
              if(res.data.length>0){
                that.condition.machineNoId = that.machineNoList[0].id
              }else{
                that.condition.machineNoId = '';
              }


            }
          })
        },

        /*根据科室查找房间*/
        searchMachineFn(){
          that.machineTypeList.map(function(item){
            if(that.condition.machineTypeId == item.id){
              that.condition.type = item.typeCode
            }
          })
          var data={
            departmentId:that.condition.departmentId,
            type:that.condition.type,
            machineId:that.condition.machineNoId
          }
          searchMachine(data).then((res)=>{
            if(res.code==200 && res.success){
              that.roomList = res.data;
            }
          })
        },
        closeCzsb(){
          that.$emit('getczsbStatus',!that.czsbStatus);
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
 <style scoped lang="scss">
 .search-content{
   margin:0 122px 0 116px;
   border:0;
   .form{
     width:auto;
     .search-item{
       display:flex;
       flex-direction: row;
       margin-bottom:16px;
       .search-item-left{
         // width:120px;
         display:flex;
         justify-content: flex-end;
         align-items: center;
         margin-right:15px;
         span{
           font-size:21px;
           font-family:PingFangSC-Medium,PingFang SC;
           font-weight:500;
           color:rgba(0,0,0,1);
         }
       }
       .search-item-right{
         display:flex;
         flex-direction: row;
         align-items: center;
         .select{
           margin-right:30px;
           width:216px;
         }
         .search-btn{
           display:block;
           // width:137px;
           padding: 0 5px;
           height:44px;
           line-height:44px;
           background:rgba(24,144,255,1);
           border-radius:2px;
           font-size:18px;
          font-family:PingFangSC-Regular,PingFang SC;
          font-weight:400;
          color:rgba(255,255,255,1);
         }
       }
     }
     .rooms{
       background:rgba(255,255,255,1);
        border-radius:8px;
        border:2px solid rgba(24,144,255,1);
        display:flex;
        flex-wrap: wrap;
        width:728px;
        margin:0 auto;
        .room{
          width:80px;
          height:78px;
          border-right:1px solid #979797;
          border-bottom:1px solid #979797;
          display:flex;
          flex-direction: column;
          align-items: center;
          span{
            font-size:20px;
            font-family:PingFangSC-Medium,PingFang SC;
            font-weight:500;
            color:rgba(24,144,255,1);
            margin-top:14px;
            display:block;
            float:left;
          }
          img{
            width:24px;
            margin-top:3px;
          }
        }
        .room:nth-child(-n+9){
          border-top:none;
        }
        .room:nth-child(9n){
          border-right:none;
        }
     }
     .res-row{
       width:100%;
       height:44px;
       .res-tips{
         float:right;
         display:flex;
         margin-top:16px;
         img{
           width:24px;height:24px;
           margin-right:10px;
         }
         span{
           font-size:16px;
          font-family:PingFangSC-Regular,PingFang SC;
          font-weight:400;
          color:rgba(35,35,35,1);
          line-height:24px;
         }
       }
     }
     .result-h1{
       display:flex;
       width:728px;
       h6{
         font-size:21px;
        font-family:PingFangSC-Medium,PingFang SC;
        font-weight:500;
        color:rgba(0,0,0,1);
        margin:0;
        em{
          font-style: normal;
          color:#FF0000;
          line-height:24px;
        }
        span{
          font-size:16px;
          line-height:24px;
          font-family:PingFangSC-Regular,PingFang SC;
          font-weight:400;
          color:rgba(109,114,120,1);
        }
       }
     }
     .result-h2{
       text-align:left;
       width:728px;
       margin-top:10px;
       font-size:16px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(109,114,120,1);
     }
   }
}
.search-item-right /deep/ .el-input__inner{
  height:36px;
}
 </style>



