<template>
  <div class="device-all">
    <img src="../assets/img/bg.png" alt="" class='bg'>
    <Topbar>
      <i class='iconfont'>&#xe62f;</i>
      <span>{{$t("device.title")}}</span>
    </Topbar>
    <div class="device-main">
      <div class="device-choose">
        <div class="device-row">
          <div class="row-left">
            <span class="choose-title">{{$t("device.cxxz")}}:</span>
            <el-select class='select' v-model='condition.usageState' filterable :placeholder="$t('message.select')" @change='selectUsageState'>
             <el-option
               v-for="item in statusList"
               :key="item.usageState"
               :label="item.name"
               :value="item.usageState">
             </el-option>
           </el-select>
          </div>
          <div class="row-right">
            <div class="buttons">
              <a href="javascript:void(0)" @click='czsbStatus = !czsbStatus'>{{$t("device.czsb")}}</a>
              <a href="javascript:void(0)" @click='tjsbStatus = !tjsbStatus'>{{$t("device.tjsb")}}</a>
              <a href="javascript:void(0)" @click='extendFn'>{{$t("device.dc")}}</a>
            </div>
          </div>
        </div>
        <div class="device-row">
          <div class="row-left">
            <span class="choose-title">{{$t("device.sbmc")}}:</span>
            <el-select class='select' v-model='condition.machineTypeId' filterable :placeholder="$t('message.select')" @change='selectMachineType'>
             <el-option
               v-for="item in machineTypeList"
               :key="item.id"
               :label="item.name"
               :value="item.id">
             </el-option>
           </el-select>
          </div>
          <div class="row-right"></div>
        </div>
      </div>
      <div class="main">
        <el-table :data="devicesList" style="width: 100%" :empty-text="$t('message.noData')">
          <el-table-column prop="machineNo" :label="$t('device.bh')" width="100"></el-table-column>
          <el-table-column prop="machineName" :label="$t('device.sbmc')"></el-table-column>
          <el-table-column prop="machineModel" :label="$t('device.sbxh')"></el-table-column>
          <el-table-column prop="machineNum" :label="$t('device.sn')"></el-table-column>
          <el-table-column prop="departmentName" :label="$t('device.syks')"></el-table-column>
          <el-table-column prop="usageState" :label="$t('device.zt')">
            <template slot-scope="scope">
              <span v-if='scope.row.usageState==1'>{{$t("device.ty")}}</span>
              <span v-if='scope.row.usageState!=1'>{{$t("device.zc")}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="createTime" :label="$t('device.tjrq')"></el-table-column>
          <el-table-column prop="remark" :label="$t('device.bz')"></el-table-column>
          <el-table-column prop="usageState" :label="$t('device.cz')">
            <template slot-scope="scope">
              <div class="" @click='toTy(scope.row)' style='position:relative;'>
                <el-switch class='switch' v-model='scope.row.usageState' v-if='scope.row.usageState==0' active-color="#108EE9" inactive-color="#EFEFEF" :active-value="0" :inactive-value="1" :active-text="$t('device.open')" :inactive-text="$t('device.enopen')"></el-switch>
                <el-switch class='switch' v-model='scope.row.usageState' v-if='scope.row.usageState==1' active-color="#108EE9" inactive-color="#EFEFEF" :active-value="0" :inactive-value="1" :active-text="$t('device.open')" :inactive-text="$t('device.enopen')"></el-switch>
                <el-switch class='switch' v-model='scope.row.usageState' v-if='scope.row.usageState==2' active-color="#108EE9" inactive-color="#EFEFEF" :active-value="2" :inactive-value="1" :active-text="$t('device.open')" :inactive-text="$t('device.enopen')"></el-switch>
                <div class="use-status"></div>
              </div>
            </template>
          </el-table-column>

        </el-table>
      </div>
      <el-pagination
      background
      :page-size='pageSize'
      :current-page='currentPage'
      @current-change="handleCurrentChange"
      layout="prev, pager, next"
        :total="devicesData.records">
      </el-pagination>
    </div>

    <czsb :czsbStatus='czsbStatus' v-if='czsbStatus' @getczsbStatus='getczsbStatus' :machineTypeList='machineTypeList'></czsb>
    <tjsb :tjsbStatus='tjsbStatus' v-if='tjsbStatus' :getDeviceListFn='getDeviceListFn' @gettjsbStatus='gettjsbStatus' :machineTypeList='machineTypeList'></tjsb>
    <sbty :sbtyStatus='sbtyStatus' v-if='sbtyStatus' :stopDevice='stopDevice' :getDeviceListFn='getDeviceListFn' @getsbtyStatus='getsbtyStatus'></sbty>
  </div>

</template>
<script>
import { mapState,mapActions} from 'vuex'
import czsb from "../components/device/czsb.vue"
import tjsb from "../components/device/tjsb.vue"
import sbty from "../components/device/sbty.vue"
import Topbar from '../components/topbar'
import { getMachineTypeList } from '@/api/history'
import { getDeviceList,addDevice,exportDevice } from '@/api/device'
var that = {};
export default {
  name: 'device',
  data () {
    return {
      czsbStatus:false,
      tjsbStatus:false,
      sbtyStatus:false,
      currentRowusageState:'',
      statusList:[
        {name:that.$t("device.status1"),usageState:-1},
        {name:that.$t("device.status2"),usageState:2},
        {name:that.$t("device.status3"),usageState:1},
      ],
      condition:{
        usageState:-1,
        machineTypeId:''
      },
      devicesList:[],
      stopDevice:{},
      devicesData:[],
      machineTypeList:[],
      pageSize:that.global.pageSize,
      currentPage:that.global.currentPage,
      language:that.$store.state.login.language,
    }
  },
  beforeCreate(){
      that = this;
  },
  mounted:function(){
    console.log(that.pageSize)
    that.getMachineTypeListFn();
  },
  methods:{
    handleCurrentChange(value){
      console.log(value)
      that.currentPage = value;
      that.getDeviceListFn();
    },
    gettjsbStatus(tjsbStatus){
      that.tjsbStatus = tjsbStatus;
    },
    getczsbStatus(czsbStatus){
      that.czsbStatus = czsbStatus;
    },
    getsbtyStatus(sbtyStatus){
      that.sbtyStatus = sbtyStatus;
    },
    selectMachineType(id){
      that.condition.machineTypeId = id;
      that.currentPage = that.global.currentPage;
      that.getDeviceListFn();
    },
    selectUsageState(status){
      that.condition.usageState = status;
      that.currentPage = that.global.currentPage;
      console.log(status)
      that.getDeviceListFn();
    },
    /*获取设备类型*/
    getMachineTypeListFn(){
      getMachineTypeList().then((res)=>{
        if(res.code==200 && res.success){
          that.machineTypeList = res.data;
          that.condition.machineTypeId = that.machineTypeList[0].id;
          that.getDeviceListFn();
        }
      })
    },
    toTy(row){
      console.log(row)
      that.stopDevice = row;
      if(row.usageState==0){
        that.sbtyStatus = !that.sbtyStatus;
      }else if(row.usageState==2){
        that.error(that.$t("device.err.sbsyz"));
      }else if(row.usageState==1){
        that.error(that.$t("device.err.btgqy"));
      }
    },
    /*查询设备*/
    getDeviceListFn(){
      var data={
        pageSize:that.global.pageSize,
        page:that.currentPage,
        machineTypeId:that.condition.machineTypeId
      }
      if(that.condition.usageState!=-1){
        data['usageState'] = that.condition.usageState;
      }
      getDeviceList(data).then((res)=>{
        console.log(res.data.list)
        if(res.code==200 && res.success){
          that.devicesData = res.data;
          that.devicesList = res.data.list;
        }
      })
    },
    /*导出设备*/
    extendFn(){
      console.log('2233')
      this.$confirm(that.$t("device.err.qrdc")+'?', that.$t("device.err.czts")+'：', {
        confirmButtonText: that.$t("button.confirm"),
        cancelButtonText: that.$t("button.close"),
        type: 'warning',
        center: true
      }).then((res) => {
        console.log(res)
        var languageType;
        if(that.language=="zh"){
          languageType = 0;
        }else if(that.language=="en"){
          languageType = 1;
        }
        console.log()
        var data={
          machineTypeId:that.condition.machineTypeId,
          language:languageType,
          token:that.$store.state.login.token
        }
        if(that.condition.usageState!=-1){
          data['usageState'] = that.condition.usageState;
        }
        exportDevice(data).then((res)=>{
            console.log(res)
            if(res.data.rCode=='success'){
              that.success(that.$t("message.OperSuccess"));
            }
        })
      }).catch(() => {

      });
    },
    /*查找定位*/

  },
  components:{
    czsb,
    tjsb,
    sbty,
    Topbar
  },
}
</script>
<style lang="scss">
html,body{
width:100%;
height:100%;
  .device-all{
    width:100%;
    background-size:cover;
    height:100%;
    position:relative;
    padding-top:40px;
    .device-main{
      position:relative;
      margin:0 auto;
      width:83%;
      .device-choose{
        padding-top: 47px;
        display:flex;
        flex-direction: column;
        .device-row{
          display:flex;
          flex-direction: row;
          justify-content: space-between;
          margin-bottom:23px;
          .row-left{
            width:50%;
            display:flex;
            flex-direction: row;
            .choose-title{
              font-size:18px;
              font-family:PingFangSC-Medium,PingFang SC;
              font-weight:600;
              color:rgba(0,0,0,1);
              line-height:36px;
              margin-right:7px;
              height:36px;
            }
            .choose-title1{
              margin:0 17px;
            }
          }
          .row-right{
            width:50%;
            display:flex;
            flex-direction: row;
            justify-content: flex-end;
            .buttons{
              display:flex;
              flex-direction: row;
              a{
                display:block;
                width:109px;
                height:35px;
                background:rgba(24,144,255,1);
                border-radius:2px;
                font-size:14px;
                font-family:PingFangSC-Regular,PingFang SC;
                font-weight:400;
                color:rgba(255,255,255,1);
                line-height:35px;
                text-align:center;
                margin-right:12px;
              }
              a:last-of-type{
                margin-right:0;
              }
            }
          }
        }
      }
      .main th,.main td{
        text-align:center;
        .options{
          display:flex;
          flex-direction: row;
          justify-content: center;
          em{
            font-style: normal;
            width:1px;
            height:20px;
            display:block;
            margin:0 15px;
            background:#D8D8D8;
          }
          .iconfont{
            color:#000;
            font-size:22px;
          }
        }
      }
      .main th{
        font-size:16px;
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight:600;
        color:rgba(0,0,0,1);
        .cell{
          word-wrap: break-word;
          word-break: keep-all;
        }
      }
      .main td{
        font-size:14px;
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight:400;
        color:#A3A3A3;
      }
    }
  }
}
.main /deep/ .el-switch__label--left{
  color: #fff;
  position: absolute;
  left: 16px;
  z-index: -1111;
}
.el-switch .el-switch__label--left{
  margin-right: -2px;
}
.main /deep/ .el-switch__label--right{
  position: absolute;
  left: -5px;
  color: #fff;
  z-index: -1111;
}
.main /deep/ .el-switch__label--right.is-active{
  z-index: 111;
  color: #fff !important;
}
.main /deep/ .el-switch__label--left.is-active{
  z-index: 111;
  color: #9c9c9c !important;
}
.main /deep/ .el-pagination{
  display:flex;
  justify-content: flex-end;
  padding:21px 0;
  float:right;
}
.main .el-pagination{
  display:flex;
  justify-content: flex-end;
  padding:21px 0;
  float:right;
}
.use-status{
  position: absolute;
  width: 100%;
  height: 100%;
  top:0;
  top:50%;
  transform:translate(0,-50%);
  z-index:100000;
  cursor: pointer;
}
</style>



