import service from '../utils/http'
/*查询所有设备*/
export function getDeviceList(data) {
  return service({
    url: '/machine/machine',
    method: 'get',
    params:data
  })
}
/*新增设备*/
export function addDevice(data) {
  return service({
    url: '/machine/machine',
    method: 'post',
    data:data
  })
}
/*查找设备*/
export function searchDevice(data) {
  return service({
    url: '/machine/searchMachine',
    method: 'get',
    params:data
  })
}
/*导出设备*/
export function exportDevice(data) {
  return service({
    url: '/machine/export',
    method: 'get',
    params:data
  })
}
/*查找设备*/
export function searchMachine(data) {
  return service({
    url: '/machine/searchMachine',
    method: 'get',
    params:data
  })
}
/*获取设备编号*/
export function getMachineNo(data) {
  return service({
    url: '/machine/machineListNo',
    method: 'get',
    params:data
  })
}
/*获取设备型号*/
export function getMachineModel(data) {
  return service({
    url: '/machine/machineModel',
    method: 'get',
    params:data
  })
}
/*停用设备*/
export function deleteDevice(data) {
  return service({
    url: '/machine/machine',
    method: 'delete',
    data:data
  })
}



